.checkbox {
  display: none;
}

.check {
  width: 40px;
  height: 24px;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  transition: background .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.check:after {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 3px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.checkbox:checked ~ .check {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  transition: background .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.checkbox:checked ~ .check:after {
  transform: translateX(20px);
}

